@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}


.section-gallery-slide {
	.slider-container {
		position: relative;
	}
	.tns-nav {
		position: relative;
		bottom: 0;
		margin-top: 20px;
	}
	.block-content {
		padding: 15px;
	}
	div.product-title {
		font-size: 20px;
		font-weight: 500;
		line-height: 1.2em;
	}
	.product-weight {
		font-size: 20px;
		font-weight: 500;
	}
	.product-price {
		margin: 12px 0;
		font-size: 20px;
		font-weight: 600;
		line-height: 1.15em;
		color: inherit;
	}
	price-item {
		color: inherit;
	}
	.price-compare {
		font-size: 20px;
		font-weight: 400;
		position: relative;
		top: 2px;
	}
	form .btn {
		font-size: 16px;
		padding-top: 7px;
		padding-bottom: 7px;
		height: 36px;
		svg {
			display: none;
		}
		&.loading {
			position: relative;
			.icon-loading {
				display: block;
				width: 15px;
				height: 15px;
				@include ab(50%,50%,n,n);
				@include margin-start(-7.5px);
				margin-top: -7.5px;
				path {
					fill: #fff;
				}
			}
			span {
				opacity: 0;
				visibility: hidden;
			}
		}
	}
	.button-group {
		a {
			height: 36px;
			font-size: 16px;
			padding-top: 7px;
			padding-bottom: 7px;
		}
	}
	.text {
		font-weight: 500;
	}
	@media (min-width: 750px) {
		.block-content {
			padding: 30px;
		}
		.product-price {
			margin: 20px 0;
			font-size: 25px;
		}
	}
} 